import React, { Component } from "react"
import Link from "gatsby-link"
import PropTypes from "prop-types"
import styled from "styled-components"
import {PRIMARY_TEXT, SMALLCHAT_SHADOW} from '../components/consts/colors'

const Container = styled.div`
   margin: 0 auto;
   /* width: 95%; */
   max-width: 1200px;
   padding: 1.25rem 1rem;
`
const Header = styled.header`
   margin-bottom: 1.5rem;
`
const listLink = styled.li`
  display: inline-block,
  margin-right: 1rem;
  text-decoration: none;
`
const buttonLink = styled.li`
  display: inline-block;
  margin-right: 1rem;
  background-color: white;
  color: white;
  box-shadow: 0px 3px 8px ${SMALLCHAT_SHADOW};
  :hover {
    transform: scale(1.1);
    box-shadow: 0px 5px 10px ${SMALLCHAT_SHADOW};
    transition: 200ms ease;
  }
`
const HeaderTitle = styled.h3`
  display: inline;
`
const ListLink = props =>
  <listLink>
    <Link to={props.to}>
      {props.children}
    </Link>
  </listLink>

const ButtonLink = props =>
  <buttonLink>
    <Link to={props.to}>
      {props.children}
    </Link>
  </buttonLink>

export default ({ children }) =>
  <Container>
    <Header>
      <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
        <HeaderTitle>Smallchat</HeaderTitle>
      </Link>
      <ul style={{ listStyle: `none`, float: `right` }}>
        <ListLink to="/">what is Smallchat</ListLink>
        <ListLink to="/about/">Pricing</ListLink>
        <ListLink to="/contact/">Features</ListLink>
        <ButtonLink to="/contact/">Dashboard</ButtonLink>
      </ul>
    </Header>
    {children()}
  </Container>



